import React, { Component } from 'react'
import { connect } from 'react-redux'
import { View } from '@tarojs/components'
import NavBar from '@/components/NavBar'
import Detail from './components/Detail'
import Empty from './components/Empty'
import './resume.less'

interface ResumeProps {
  resume,
  dispatch
}

/**我的简历 */
@connect(({ resume, dispatch }) => ({
  resume,
  dispatch
}))
export default class Resume extends Component<ResumeProps> {
  componentDidShow() {
    this.props.dispatch({
      type: 'resume/queryJobIntensionsListDictionary', payload: {}
    }).then(res => {
      this.props.dispatch({
        type: 'resume/queryResumeDetail', payload: {}
      }).catch(err => {
        console.log(err)
      })
    }).catch(err => {
      console.log(err)
    })
  }

  render() {
    let { resume } = this.props;
    return (
      <View className='index'>
        <NavBar title="我的简历" />
        { resume.ResumeDetail.identity?.idcard ? <Detail /> : <Empty />}
      </View>
    )
  }
}

