import React, { FC } from 'react'
import { View, Text, Image } from '@tarojs/components'
import Taro from "@tarojs/taro"
import { AtButton } from 'taro-ui'
import EmptyPic from "@/images/empty.png"
import EmptyList from "@/images/list-empty.png"
import commonUtil from '@/utils/common'
import './index.less'


/**空白简历 */
const Empty: FC = () => {
  function goAdd() {
    Taro.navigateTo({
      url: '/pages/resume/addResume'
    })
  }
  function gotoLogin() {
    Taro.navigateTo({
      url: '/pages/login/login'
    })
  }
  let token = commonUtil.getToken()
  return (
    <View className="fixedView">
      <View className='resumeEmptyView'>
        <Image src={EmptyPic} className="emptyPic" mode='aspectFit' />
        <View className="empty">
          <Image src={EmptyList} mode='aspectFit' className="image" />
          <Text className="text">这里空空如也，赶紧去{token ? '录入简历吧' : '登录吧'}~</Text>
        </View>
      </View>
      <View className="fixedBtnView">
        {token ? <AtButton className="fixedBtn" onClick={goAdd} >录入我的简历</AtButton> : <AtButton className="fixedBtn" onClick={gotoLogin} >点击去登录</AtButton>}
      </View>
    </View>
  )
}

export default Empty
