import React, { FC } from 'react'
import Taro from '@tarojs/taro'
import { connect } from 'react-redux'
import { View, Text, Image, Video } from '@tarojs/components'
import { AtIcon, AtButton, AtTimeline } from 'taro-ui'
import Card from '@/components/Card'
import { DefaultUserPic } from "@/common/constant"
import moment from 'moment'
import Constant from '@/common/constant'
import './index.less'


interface DetailProps {
  resume,
  dispatch
}

/**简历详情 */
const Detail: FC<DetailProps> = (props: DetailProps) => {

  function onImageClick(item: string, list: string[] = [item]) {
    Taro.previewImage({
      current: item,
      urls: list,
    });
  }

  function goAdd() {
    Taro.navigateTo({
      url: '/pages/resume/addResume'
    })
  }

  let { ResumeDetail, JobIntensionsList = [] } = props.resume;
  let baseInfo = [{
    key: '身份证',
    value: ResumeDetail.identity?.idcard
  }, {
    key: '籍贯',
    value: ResumeDetail.detail?.nativePlace
  }, {
    key: '生肖',
    value: ResumeDetail.identity?.chineseZodiac
  }, {
    key: '星座',
    value: ResumeDetail.identity?.constellation
  }, {
    key: '民族',
    value: ResumeDetail.identity?.nation
  }, {
    key: '户籍',
    value: `${ResumeDetail.identity?.nativePlaceProvince || ''}${ResumeDetail.identity?.nativePlaceCity || ''}${ResumeDetail.identity?.nativePlaceDistrict || ''}${ResumeDetail.identity?.permanentAddress || ''}`
  }, {
    key: '学历',
    value: ResumeDetail.detail?.education
  }, {
    key: '婚姻',
    value: ResumeDetail.detail?.married
  }]

  let jobInfo = [{
    key: '工作类型',
    value: JobIntensionsList.filter(item => (ResumeDetail.jobIntensionsList || []).includes(item.name)).map(item => item.name).join('、') || ''
  }, {
    key: '工作时间',
    value: ResumeDetail.workTime || ''
  }, {
    key: '工作经验',
    value: ResumeDetail.skills?.workingLife ? Constant.WorkingLifeDataList.get(ResumeDetail.skills?.workingLife) : ''
  }, {
    key: '期望薪资',
    value: `${ResumeDetail.skills?.minExpectSalary || ''}${ResumeDetail.skills?.maxExpectSalary ? `-${ResumeDetail.skills?.maxExpectSalary}` : ''}`
  }]

  let skillInfo = [
    {
      key: '个人技能',
      value: (ResumeDetail.skills?.composite || []).join('、') || ''
    },
    /*{
      key: '掌握菜系',
      value: (ResumeDetail.skills?.cuisine || []).join('、') || ''
    },
    {
      key: '做饭能力',
      value: ResumeDetail.skills?.cookAble || ''
    }, {
      key: '语言能力',
      value: (ResumeDetail.skills?.language || []).join('、') || ''
    }, {
      key: '普通话水平',
      value: ResumeDetail.skills?.mandarinAble || ''
    }*/
  ]

  let otherInfo = [{
    key: '现居地',
    value: `${ResumeDetail.address?.province || ''}${ResumeDetail.address?.city || ''}${ResumeDetail.address?.district || ''}${ResumeDetail.address?.address || ''}`
  }, {
    key: '身高',
    value: ResumeDetail.detail?.height || '',
    unit: 'cm'
  }, {
    key: '体重',
    value: ResumeDetail.detail?.weight || '',
    unit: 'kg'
  }, {
    key: '信仰',
    value: ResumeDetail.detail?.religion || ''
  }, {
    key: '签证',
    value: ResumeDetail.detail?.visa || ''
  }, {
    key: '血型',
    value: ResumeDetail.detail?.bloodType || ''
  }, {
    key: '子女数量',
    value: ResumeDetail.detail?.children || ''
  }, {
    key: '子女年龄',
    value: ResumeDetail.detail?.childrenAge || ''
  }, {
    key: '配偶职业',
    value: ResumeDetail.detail?.spouseJob || ''
  }, {
    key: '紧急联系电话',
    value: ResumeDetail.detail?.emergencyPhone || ''
  }]
  let workingExperiencesList = (ResumeDetail?.workingExperiencesList || []).map(item => ({
    title: `${moment(item.startTime).format('YYYY-MM-DD')}至${moment(item.endTime).format('YYYY-MM-DD')}`,
    content: [item.serviceName, item.experienceDiscription],
    color: 'red'
  }))
  return (
    <View className='index fixedView'>
      <View className="resumeTop">
        <View className="myInfo">
          <Image src={ResumeDetail.auntHeadPhoto || DefaultUserPic} className="headPhoto" mode='aspectFill' onClick={() => {
            onImageClick(ResumeDetail.auntHeadPhoto || DefaultUserPic, [ResumeDetail.auntHeadPhoto || DefaultUserPic])
          }} />
          <View className="myInfoCon">
            <View className='name'>
              <Text className="Text">{ResumeDetail.identity?.name}</Text>
              <View className='state'>{ResumeDetail.jobStatus}</View>
            </View>
            <Text
              className='address'>{ResumeDetail.identity?.age}岁 {ResumeDetail.detail?.nativePlace && ` | ${ResumeDetail.detail?.nativePlace}人`}</Text>
            <Text
              className='desc'>{(ResumeDetail.description?.description) ? `${ResumeDetail.description?.description}` : '暂无描述...'}</Text>
          </View>
        </View>
        {ResumeDetail.completionDegree != '100%' && <View className="myTip" onClick={goAdd}>
          简历完整度{ResumeDetail.completionDegree}，去完善我的简历 &#62;
        </View>}
      </View>
      {baseInfo.filter(item => item.value).length > 0 && <View className="basicInfo">
        <Card title="基本信息">
          {baseInfo.map((item, index) => {
            return (item.value && <View className="listItem" key={index}>
              <Text className="listTitle">{item.key}</Text>
              <Text className="listText">{item.value}</Text>
            </View>)
          })}
        </Card>
      </View>}
      {baseInfo.filter(item => item.value).length > 0 && <View className="jobInfo">
        <Card title="求职信息">
          {jobInfo.map((item, index) => {
            return (item.value && <View className="listItem" key={index}>
              <Text className="listTitle">{item.key}</Text>
              <Text className="listText">{item.value}</Text>
            </View>)
          })}
        </Card>
      </View>}
      {(ResumeDetail.skills?.auntCert || []).length > 0 && <View className="certificateInfo">
        <Card title="证书信息">
          <View className="certificateList">
            {(ResumeDetail.skills?.auntCert || []).map((item, index) => (<View className="certificateItem" key={index}>
              <AtIcon value='star' size='18' color='#F2544E'></AtIcon>
              <Text className="text">{item}</Text>
            </View>)
            )}
          </View>
        </Card>
      </View>}
      {skillInfo.filter(item => item.value).length > 0 && <View className="skillInfo">
        <Card title="技能信息">
          {skillInfo.map((item, index) => {
            return (item.value && <View className="listItem" key={index}>
              <Text className="listTitle">{item.key}</Text>
              <Text className="listText">{item.value}</Text>
            </View>)
          })}
        </Card>
      </View>}
      {otherInfo.filter(item => item.value).length > 0 && <View className="otherInfo">
        <Card title="其他信息">
          {otherInfo.map((item, index) => (item.value && <View className="listItem" key={index}>
            <Text className="listTitle">{item.key}</Text>
            <View className="listText">{item.value}{item.unit && <Text className="listUnit">{item.unit}</Text>}</View>

          </View>)
          )}
        </Card>
      </View>}
      {(ResumeDetail?.workingExperiencesList || []).length > 0 && <View className="otherInfo">
        <Card title="从业信息">
          <AtTimeline
            pending
            items={workingExperiencesList}
            className="timeline"
          >
          </AtTimeline>
        </Card>
      </View>}
      {((ResumeDetail.photoVideo?.photos || []).length > 0 || (ResumeDetail.photoVideo?.videos || []).length > 0) &&
        <View className="picInfo">
          <Card title="照片视频">
            <View className='at-row at-row--wrap picList'>
              {
                (ResumeDetail.photoVideo?.photos || []).map(item => (
                  <View className='at-col at-col-4' key={item}><Image className='img' src={item} mode="aspectFill"
                    onClick={() => {
                      onImageClick(item, ResumeDetail.photoVideo?.photos)
                    }} /></View>
                ))
              }
              {
                (ResumeDetail.photoVideo?.videos || []).map(item => (
                  <View className='at-col at-col-4' key={item}><Video className='video' src={item} controls={true} /></View>
                ))
              }
            </View>
          </Card>
        </View>}
      {(ResumeDetail.photoVideo?.certPhotos || []).length > 0 && <View className="picInfo">
        <Card title="证书照片">
          <View className='at-row at-row--wrap picList'>
            {
              (ResumeDetail.photoVideo?.certPhotos || []).map(item => (
                <View className='at-col at-col-4' key={item}><Image className='img' src={item} mode="aspectFill"
                  onClick={() => {
                    onImageClick(item, ResumeDetail.photoVideo?.certPhotos)
                  }} /></View>
              ))
            }
          </View>
        </Card>
      </View>
      }

      <View className="fixedBtnView">
        <AtButton type='primary' className="fixedBtn" onClick={goAdd}>编辑我的简历</AtButton>
      </View>
    </View>
  )
}

export default connect(({ resume, dispatch }) => ({
  resume,
  dispatch
}))(Detail)
